.gpt3__features-container__feature{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    margin: 1rem;
    /* background-color: red; */
}   

.gpt3__features-container__feature-title{
    flex: 1;
    margin-right: 1rem;
    max-width: 180px;
}

.gpt3__features-container__feature-title h1{
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 24px;
    font-weight: 800;
    color: #ffff;
}

.gpt3__features-container__feature-title div{
    width: 38px;
    height: 3px;
    background:var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}

.gpt3__features-container_feature-text{
    flex: 2;
    max-width: 390px !important;
    display: flex;
}


.gpt3__features-container_feature-text p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}

@media screen and (max-width: 550px) {
    .gpt3__features-container__feature-title h1 {
        font-size: 13px;
        line-height: 21px;
    }

    .gpt3__features-container_feature-text p {
        font-size: 11px;
        line-height: 18px;
    }

    .gpt3__features-container__feature {
        margin: 1rem 0;
    }
}